.cover{width:64px;height:64px;border-radius:5px;display:inline-block;background-position:50%;background-size:auto 80%;background-color:#f5f5f9;background-repeat:no-repeat;float:left}.commodity{border-radius:5px;border:1px solid #999;padding:15px 1.34vw;margin-top:15px;overflow:hidden}.commodity-des{padding-left:5px;display:inline-block;float:left}.commodity-title{line-height:16px}.justify,.show-justify{padding-top:5px;color:#4d8fe6}.justify :global(.am-icon-md){transition:all .3s;transform:translateY(6px) rotate(0deg)}.show-justify :global(.am-icon-md){transition:all .3s;transform:translateY(6px) rotate(-180deg)}.stepper{float:right;width:16vw;min-width:100px;position:absolute;bottom:0;right:0}.commodity-main{overflow:hidden;position:relative}.btns{box-sizing:border-box;height:0;overflow:hidden;transition:all .1s;display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between}.btns :global(.am-button-small){width:25%}


